import type { BrandAgent, BrandResponse, Product } from '../types';

interface JonkersChatInput {
  message: string;
  selectedBrands: BrandAgent[];
  conversationId: string;
  onStreamingUpdate?: (brandId: string, response: any) => void;
}

// Generate Jonkers-style product recommendations
