// Generate Jonkers-style responses with warm, editorial tone
const generateJonkersResponse = async (
  client: any,
  agent: BrandAgent,
  message: string,
  conversationId: string
): Promise<BrandResponse> => {
  const startTime = Date.now();

  const systemPrompt = `You are ${agent.name}, a thoughtful AI stylist for Jonkers - a brand focused on minimal, timeless design and intentional living.

Your role: ${agent.description}

Your specialties: ${agent.specialties.join(', ')}.

Tone & Style:
- Warm, editorial, never pushy
- Use language like "Here's something that might speak to you" or "I found pieces that feel right for this moment"
- Focus on how items make people feel, not just what they are
- Mention materials, craftsmanship, and the story behind pieces
- Be conversational but refined
- Use phrases like "thoughtfully made," "speaks to your style," "for moments that matter"

When recommending products:
1. Connect emotionally with the user's request
2. Describe how pieces fit into their life
3. Mention quality, materials, and craftsmanship
4. Focus on timeless appeal over trends
5. Keep responses concise but warm

Example tone: "I found something that might suit your calm, minimalist taste today. This piece feels like it was made for quiet mornings and thoughtful moments."`;

  const messages = [
    { role: 'system', content: systemPrompt },
    { role: 'user', content: message }
  ];

  try {
    const response = await client.run('openai/gpt-4o', {
      input: { messages }
    });

    const responseTime = Date.now() - startTime;
    const content = response.output[0] as string;
    
    const products = generateJonkersProducts(agent, message);
    
    let confidence = agent.satisfactionScore;
    const messageLower = message.toLowerCase();
    
    // Boost confidence for specialty matches
    if (agent.specialties.some(specialty => 
      messageLower.includes(specialty.toLowerCase()) || 
      specialty.toLowerCase().includes(messageLower)
    )) {
      confidence += 5;
    }
    
    // Boost for Jonkers-style keywords
    const jonkersKeywords = ['minimal', 'timeless', 'thoughtful', 'intentional', 'slow', 'mindful', 'quality', 'crafted'];
    if (jonkersKeywords.some(keyword => messageLower.includes(keyword))) {
      confidence += 3;
    }
    
    if (responseTime < 1000) confidence += 2;
    confidence = Math.min(100, confidence);

    return {
      brandId: agent.id,
      content,
      products,
      confidence,
      responseTime,
      timestamp: Date.now(),
      isStreaming: false
    };
  } catch (error) {
    console.error(`Agent ${agent.id} failed:`, error);
    
    return {
      brandId: agent.id,
      content: `I'm taking a moment to gather my thoughts. In the meantime, let me share some pieces that might speak to you.`,
      products: generateJonkersProducts(agent, message),
      confidence: 0,
      responseTime: Date.now() - startTime,
      timestamp: Date.now(),
      isStreaming: false
    };
  }
};

// Main Jonkers chat function
export const runJonkersChat = async (
  client: any,
  input: JonkersChatInput
): Promise<BrandResponse[]> => {
  // Simulate streaming start for each agent
  if (input.onStreamingUpdate) {
    input.selectedBrands.forEach(agent => {
      input.onStreamingUpdate!(agent.id, {
        brandId: agent.id,
        content: '',
        products: [],
        confidence: 0,
        responseTime: 0,
        timestamp: Date.now(),
        isStreaming: true
      });
    });
  }

  // Run all agent queries in parallel
  const agentPromises = input.selectedBrands.map(agent =>
    generateJonkersResponse(client, agent, input.message, input.conversationId)
  );

  const responses = await Promise.all(agentPromises);

  // Update streaming responses with final results
  if (input.onStreamingUpdate) {
    responses.forEach(response => {
      input.onStreamingUpdate!(response.brandId, response);
    });
  }

  // Sort by confidence and return
  return responses.sort((a, b) => b.confidence - a.confidence);
};

// Legacy compatibility functions
export const runBrandAgentChat = runJonkersChat;

export const runFederatedSearch = async (client: any, input: any) => {
  const brandInput: JonkersChatInput = {
    message: input.query,
    selectedBrands: input.selectedAgents,
    conversationId: `legacy_${Date.now()}`,
    onStreamingUpdate: input.onStreamingUpdate
  };

  const responses = await runJonkersChat(client, brandInput);

  return {
    queryId: `search_${Date.now()}`,
    query: input.query,
    responses: responses.map(r => ({
      agentId: r.brandId,
      content: r.content,
      sources: [],
      confidence: r.confidence,
      responseTime: r.responseTime,
      timestamp: r.timestamp,
      isStreaming: r.isStreaming
    })),
    synthesizedResponse: responses.length > 0 ? responses[0].content : '',
    totalResponseTime: Math.max(...responses.map(r => r.responseTime)),
    timestamp: Date.now()
  };
};
