import React, { useState } from 'react';
import type { BrandAgent, BrandCategory } from '../types';
import { ASSETS } from '../assetManifest';
import { Palette, Sparkles, Clock, Users, ChevronDown, ChevronRight, Heart, Zap, Menu, X, Crown } from 'lucide-react';

interface JonkersAgentPanelProps {
  selectedBrands: BrandAgent[];
  onBrandSelect: (brands: BrandAgent[]) => void;
  collapsed: boolean;
  onToggleCollapse: () => void;
}

const JonkersAgentPanel: React.FC<JonkersAgentPanelProps> = ({ 
  selectedBrands, 
  onBrandSelect,
  collapsed,
  onToggleCollapse
}) => {
  const [expandedCategories, setExpandedCategories] = useState<Set<BrandCategory>>(new Set(['fashion', 'lifestyle']));
  const [searchTerm, setSearchTerm] = useState('');

  const jonkersAgents: BrandAgent[] = [
    {
      id: 'jonkers-fashion',
      name: 'Fashion Curator',
      category: 'fashion',
      description: 'Minimal, timeless pieces that transcend seasons. Curates clothing that speaks to your authentic self.',
      specialties: ['Minimalist Clothing', 'Sustainable Fashion', 'Timeless Pieces', 'Capsule Wardrobes'],
      isFeatured: true,
      responseTime: 650,
      satisfactionScore: 98,
      totalInteractions: 15200,
      icon: ASSETS.agents.fashion.path,
      commissionRate: 12.0,
      averageOrderValue: 185,
      conversionRate: 16.8
    },
    {
      id: 'jonkers-lifestyle',
      name: 'Lifestyle Stylist',
      category: 'lifestyle',
      description: 'Thoughtful objects for intentional living. Discovers pieces that enhance your daily rituals.',
      specialties: ['Home Essentials', 'Wellness Products', 'Mindful Living', 'Daily Rituals'],
      isFeatured: true,
      responseTime: 720,
      satisfactionScore: 96,
      totalInteractions: 12800,
      icon: ASSETS.agents.lifestyle.path,
      commissionRate: 14.5,
      averageOrderValue: 125,
      conversionRate: 14.2
    },
    {
      id: 'jonkers-art',
      name: 'Art Advisor',
      category: 'art',
      description: 'Curated art and design pieces that inspire. Finds works that resonate with your aesthetic vision.',
      specialties: ['Contemporary Art', 'Design Objects', 'Ceramics', 'Photography'],
      isFeatured: false,
      responseTime: 890,
      satisfactionScore: 94,
      totalInteractions: 8900,
      icon: ASSETS.agents.art.path,
      commissionRate: 18.0,
      averageOrderValue: 320,
      conversionRate: 11.5
    },
    {
      id: 'jonkers-wellness',
      name: 'Wellness Guide',
      category: 'wellness',
      description: 'Holistic wellness products for mind and body. Recommends items that nurture your well-being.',
      specialties: ['Natural Skincare', 'Aromatherapy', 'Meditation Tools', 'Self-Care'],
      isFeatured: false,
      responseTime: 580,
      satisfactionScore: 97,
      totalInteractions: 11400,
      icon: ASSETS.agents.wellness.path,
      commissionRate: 16.0,
      averageOrderValue: 89,
      conversionRate: 18.3
    },
    {
      id: 'jonkers-jewelry',
      name: 'Jewelry Curator',
      category: 'jewelry',
      description: 'Delicate, meaningful jewelry pieces. Selects accessories that tell your story with subtle elegance.',
      specialties: ['Minimal Jewelry', 'Handcrafted Pieces', 'Sustainable Materials', 'Statement Accessories'],
      isFeatured: false,
      responseTime: 750,
      satisfactionScore: 95,
      totalInteractions: 9600,
      icon: ASSETS.agents.jewelry.path,
      commissionRate: 20.0,
      averageOrderValue: 145,
      conversionRate: 13.7
    }
  ];

  const categories = [
    { id: 'fashion' as BrandCategory, name: 'Fashion', icon: ASSETS.categories.fashion.path, color: 'text-stone-600' },
    { id: 'lifestyle' as BrandCategory, name: 'Lifestyle', icon: ASSETS.categories.lifestyle.path, color: 'text-amber-600' },
    { id: 'art' as BrandCategory, name: 'Art & Design', icon: ASSETS.categories.art.path, color: 'text-rose-600' },
    { id: 'wellness' as BrandCategory, name: 'Wellness', icon: ASSETS.categories.wellness.path, color: 'text-green-600' },
    { id: 'jewelry' as BrandCategory, name: 'Jewelry', icon: ASSETS.categories.jewelry.path, color: 'text-purple-600' }
  ];

  const toggleCategory = (category: BrandCategory) => {
    const newExpanded = new Set(expandedCategories);
    if (newExpanded.has(category)) {
      newExpanded.delete(category);
    } else {
      newExpanded.add(category);
    }
    setExpandedCategories(newExpanded);
  };

  const toggleAgentSelection = (agent: BrandAgent) => {
    const isSelected = selectedBrands.some(b => b.id === agent.id);
    let newSelection;
    
    if (isSelected) {
      newSelection = selectedBrands.filter(b => b.id !== agent.id);
    } else {
      newSelection = [...selectedBrands, agent];
    }
    
    onBrandSelect(newSelection);
  };

  const filteredAgents = jonkersAgents.filter(agent => 
    agent.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    agent.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
    agent.specialties.some(spec => spec.toLowerCase().includes(searchTerm.toLowerCase()))
  );

  const getAgentsByCategory = (category: BrandCategory) => {
    return filteredAgents.filter(agent => agent.category === category);
  };

  if (collapsed) {
    return (
      <nav className="w-16 bg-white/80 backdrop-blur-xl border-r border-stone-200/50 flex flex-col items-center py-6">
        <button
          onClick={onToggleCollapse}
          className="p-3 hover:bg-stone-100/50 rounded-2xl transition-colors mb-6"
        >
          <Menu className="w-5 h-5 text-stone-600" />
        </button>
        
        <div className="space-y-4">
          {selectedBrands.slice(0, 3).map((agent) => (
            <div key={agent.id} className="relative group">
              <img 
                src={agent.icon} 
                alt={agent.name} 
                className="w-10 h-10 rounded-2xl cursor-pointer hover:scale-110 transition-transform shadow-sm" 
              />
              <div className="absolute left-full ml-3 top-0 bg-stone-800 text-white text-xs rounded-xl px-3 py-2 opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap z-10 font-light">
                {agent.name}
              </div>
            </div>
          ))}
          {selectedBrands.length > 3 && (
            <div className="w-10 h-10 bg-stone-100 rounded-2xl flex items-center justify-center text-xs font-medium text-stone-600">
              +{selectedBrands.length - 3}
            </div>
          )}
        </div>
      </nav>
    );
  }

  return (
    <nav className="w-80 bg-white/80 backdrop-blur-xl border-r border-stone-200/50 flex flex-col">
      {/* Elegant Header */}
      <div className="p-8 border-b border-stone-200/50">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-lg font-light text-stone-800 flex items-center space-x-3 tracking-wide">
            <Palette className="w-5 h-5 text-amber-500" />
            <span>Style Agents</span>
          </h2>
          <button
            onClick={onToggleCollapse}
            className="p-2 hover:bg-stone-100/50 rounded-xl transition-colors"
          >
            <X className="w-4 h-4 text-stone-500" />
          </button>
        </div>
        <input
          type="text"
          placeholder="Search specialists..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full bg-stone-50/50 border border-stone-200/50 rounded-2xl px-4 py-3 text-stone-800 placeholder-stone-500 focus:outline-none focus:ring-2 focus:ring-amber-200 focus:border-transparent font-light"
        />
      </div>

      {/* Featured Agents */}
      <div className="p-8 border-b border-stone-200/50">
        <h3 className="text-sm font-medium text-stone-700 mb-4 flex items-center space-x-2 tracking-wide">
          <Crown className="w-4 h-4 text-amber-500" />
          <span>FEATURED SPECIALISTS</span>
        </h3>
        <div className="space-y-3">
          {jonkersAgents.filter(agent => agent.isFeatured).map((agent) => {
            const isSelected = selectedBrands.some(b => b.id === agent.id);
            
            return (
              <button
                key={agent.id}
                onClick={() => toggleAgentSelection(agent)}
                className={`w-full p-4 rounded-2xl border transition-all ${
                  isSelected 
                    ? 'bg-amber-50/50 border-amber-200 ring-1 ring-amber-300' 
                    : 'bg-white/60 border-stone-200/50 hover:bg-stone-50/50 hover:border-stone-300'
                }`}
              >
                <div className="flex items-center space-x-4">
                  <img src={agent.icon} alt={agent.name} className="w-10 h-10 rounded-2xl shadow-sm" />
                  <div className="flex-1 text-left">
                    <p className="font-medium text-sm text-stone-800 tracking-wide">{agent.name}</p>
                    <p className="text-xs text-stone-500 font-light">{agent.specialties[0]}</p>
                  </div>
                  <div className="flex items-center space-x-1">
                    <Heart className="w-3 h-3 text-rose-400" />
                    <span className="text-xs text-stone-600 font-light">{agent.satisfactionScore}</span>
                  </div>
                </div>
              </button>
            );
          })}
        </div>
      </div>

      {/* Agent Categories */}
      <div className="flex-1 overflow-y-auto">
        {categories.map((category) => {
          const categoryAgents = getAgentsByCategory(category.id);
          const isExpanded = expandedCategories.has(category.id);
          
          if (categoryAgents.length === 0 && searchTerm) return null;

          return (
            <div key={category.id} className="border-b border-stone-200/50">
              <button
                onClick={() => toggleCategory(category.id)}
                className="w-full flex items-center justify-between p-6 hover:bg-stone-50/30 transition-colors"
              >
                <div className="flex items-center space-x-4">
                  <img src={category.icon} alt={category.name} className="w-4 h-4" />
                  <span className="font-medium text-sm text-stone-800 tracking-wide">{category.name}</span>
                  <span className="text-xs text-stone-500 font-light">({categoryAgents.length})</span>
                </div>
                {isExpanded ? (
                  <ChevronDown className="w-4 h-4 text-stone-400" />
                ) : (
                  <ChevronRight className="w-4 h-4 text-stone-400" />
                )}
              </button>

              {isExpanded && (
                <div className="pb-3">
                  {categoryAgents.map((agent) => {
                    const isSelected = selectedBrands.some(b => b.id === agent.id);

                    return (
                      <div
                        key={agent.id}
                        className={`mx-6 mb-3 rounded-2xl border transition-all ${
                          isSelected 
                            ? 'bg-amber-50/50 border-amber-200' 
                            : 'bg-white/60 border-stone-200/50 hover:bg-stone-50/50'
                        }`}
                      >
                        <button
                          onClick={() => toggleAgentSelection(agent)}
                          className="w-full p-4 text-left"
                        >
                          <div className="flex items-start justify-between mb-3">
                            <div className="flex items-center space-x-3">
                              <img src={agent.icon} alt={agent.name} className="w-8 h-8 rounded-xl shadow-sm" />
                              <span className="font-medium text-sm text-stone-800 tracking-wide">{agent.name}</span>
                              {agent.isFeatured && (
                                <Crown className="w-3 h-3 text-amber-400" />
                              )}
                            </div>
                            <div className="flex items-center space-x-1">
                              <Heart className="w-3 h-3 text-rose-400" />
                              <span className="text-xs text-stone-600 font-light">{agent.satisfactionScore}</span>
                            </div>
                          </div>
                          
                          <p className="text-xs text-stone-600 mb-3 line-clamp-2 font-light leading-relaxed">
                            {agent.description}
                          </p>
                          
                          <div className="flex items-center justify-between text-xs text-stone-500">
                            <div className="flex items-center space-x-1">
                              <Clock className="w-3 h-3" />
                              <span className="font-light">{agent.responseTime}ms</span>
                            </div>
                            <div className="flex items-center space-x-1">
                              <Users className="w-3 h-3" />
                              <span className="font-light">{agent.totalInteractions.toLocaleString()}</span>
                            </div>
                            <div className="flex items-center space-x-1">
                              <Zap className="w-3 h-3 text-green-500" />
                              <span className="font-light">{agent.conversionRate}%</span>
                            </div>
                          </div>
                        </button>
                      </div>
                    );
                  })}
                </div>
              )}
            </div>
          );
        })}
      </div>

      {/* Selected Count */}
      {selectedBrands.length > 0 && (
        <div className="p-6 border-t border-stone-200/50 bg-stone-50/30">
          <div className="text-sm text-stone-700 mb-3 font-medium tracking-wide">
            {selectedBrands.length} specialist{selectedBrands.length !== 1 ? 's' : ''} selected
          </div>
          <div className="flex items-center justify-between">
            <button
              onClick={() => onBrandSelect([])}
              className="text-xs text-amber-600 hover:text-amber-700 font-medium tracking-wide"
            >
              Clear all
            </button>
            <div className="text-xs text-stone-500 font-light">
              Avg satisfaction: {selectedBrands.length > 0 ? 
                (selectedBrands.reduce((sum, agent) => sum + agent.satisfactionScore, 0) / selectedBrands.length).toFixed(1) : 0}%
            </div>
          </div>
        </div>
      )}
    </nav>
  );
};

export default JonkersAgentPanel;
