import React, { useState, useRef, useEffect } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import { Send, Loader2, Heart, Camera, Package, Upload, Sparkles, ShoppingBag, User, MapPin, Phone, CreditCard, Check, X } from 'lucide-react';
import { runJonkersChat } from '../utils/federatedSearch';
import type { BrandAgent, CommerceConversation, ChatMessage, Product } from '../types';

interface JonkersChatProps {
  selectedBrands: BrandAgent[];
  searchQuery: string;
  activeConversations: CommerceConversation[];
  setActiveConversations: React.Dispatch<React.SetStateAction<CommerceConversation[]>>;
}

interface OrderForm {
  name: string;
  address: string;
  phone: string;
  quantity: number;
}

const JonkersChat: React.FC<JonkersChatProps> = ({ 
  selectedBrands,
  searchQuery,
  activeConversations,
  setActiveConversations
}) => {
  const { client, useStorage } = useSubscribeDev();
  const [currentMessage, setCurrentMessage] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [activeConversationId, setActiveConversationId] = useState<string | null>(null);
  const [streamingResponses, setStreamingResponses] = useState<Map<string, any>>(new Map());
  const [showOrderForm, setShowOrderForm] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [orderForm, setOrderForm] = useState<OrderForm>({ name: '', address: '', phone: '', quantity: 1 });
  const [orderSuccess, setOrderSuccess] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  const [conversationHistory, setConversationHistory] = useStorage<CommerceConversation[]>('jonkers_conversations', []);

  useEffect(() => {
    setActiveConversations(conversationHistory);
  }, [conversationHistory, setActiveConversations]);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [activeConversations, streamingResponses]);

  useEffect(() => {
    if (searchQuery && selectedBrands.length > 0) {
      handleSendMessage(searchQuery);
    }
  }, [searchQuery]);

  const handleImageUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const imageData = e.target?.result as string;
        handleSendMessage(`I'd like to find something similar to this image: ${imageData}`);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSendMessage = async (message?: string) => {
    const messageToSend = message || currentMessage;
    if (!messageToSend.trim() || !client || isTyping || selectedBrands.length === 0) return;

    setIsTyping(true);
    setCurrentMessage('');

    try {
      const conversationId = activeConversationId || `conv_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
      
      const userMessage: ChatMessage = {
        id: `msg_${Date.now()}_user`,
        content: messageToSend,
        sender: 'user',
        timestamp: Date.now()
      };

      const updatedConversations = activeConversations.map(conv => 
        conv.id === conversationId 
          ? { ...conv, messages: [...conv.messages, userMessage], lastActivity: Date.now() }
          : conv
      );

      if (!activeConversationId) {
        const newConversation: CommerceConversation = {
          id: conversationId,
          brandIds: selectedBrands.map(b => b.id),
          messages: [userMessage],
          lastActivity: Date.now(),
          products: [],
          totalValue: 0
        };
        updatedConversations.push(newConversation);
        setActiveConversationId(conversationId);
      }

      setActiveConversations(updatedConversations);

      const responses = await runJonkersChat(client, {
        message: messageToSend,
        selectedBrands,
        conversationId,
        onStreamingUpdate: (brandId: string, response: any) => {
          setStreamingResponses(prev => new Map(prev.set(brandId, response)));
        }
      });

      const aiMessages: ChatMessage[] = responses.map(response => ({
        id: `msg_${Date.now()}_${response.brandId}`,
        content: response.content,
        sender: 'ai',
        timestamp: Date.now(),
        brandId: response.brandId,
        products: response.products,
        confidence: response.confidence
      }));

      const finalConversations = updatedConversations.map(conv => 
        conv.id === conversationId 
          ? { 
              ...conv, 
              messages: [...conv.messages, ...aiMessages], 
              lastActivity: Date.now(),
              products: [...conv.products, ...responses.flatMap(r => r.products || [])]
            }
          : conv
      );

      setActiveConversations(finalConversations);
      setConversationHistory(finalConversations.slice(0, 20));
      setStreamingResponses(new Map());
    } catch (error) {
      console.error('Jonkers chat failed:', error);
    } finally {
      setIsTyping(false);
    }
  };

  const handleOrder = (product: Product) => {
    setSelectedProduct(product);
    setShowOrderForm(true);
  };

  const handleSubmitOrder = async () => {
    if (!selectedProduct || !orderForm.name || !orderForm.address || !orderForm.phone) return;

    const orderData = {
      product: selectedProduct,
      customer: orderForm,
      total: selectedProduct.price * orderForm.quantity,
      timestamp: Date.now()
    };

    console.log('Order submitted:', orderData);
    
    setShowOrderForm(false);
    setOrderForm({ name: '', address: '', phone: '', quantity: 1 });
    setSelectedProduct(null);
    setOrderSuccess(true);
    
    setTimeout(() => setOrderSuccess(false), 3000);
  };

  const activeConversation = activeConversations.find(conv => conv.id === activeConversationId);

  if (selectedBrands.length === 0) {
    return (
      <div className="flex-1 flex flex-col items-center justify-center text-center p-12 bg-gradient-to-br from-stone-50 via-amber-50/30 to-rose-50/30">
        <div className="w-24 h-24 bg-gradient-to-br from-amber-100 to-rose-100 rounded-3xl flex items-center justify-center mb-8">
          <Sparkles className="w-12 h-12 text-stone-600" />
        </div>
        <h2 className="text-3xl font-light text-stone-800 mb-6 tracking-wide">Begin Your Style Journey</h2>
        <p className="text-stone-600 text-lg mb-4 font-light leading-relaxed max-w-md">Select style specialists from the sidebar to start discovering pieces that speak to your soul</p>
        
        <div className="mt-12 grid grid-cols-1 md:grid-cols-2 gap-8 max-w-2xl">
          <div className="bg-white/80 backdrop-blur-sm rounded-3xl p-8 border border-stone-200/50">
            <h3 className="font-medium text-stone-800 mb-4 tracking-wide">Style Conversations</h3>
            <ul className="text-sm text-stone-600 space-y-2 font-light">
              <li> "I'm looking for a minimal winter coat"</li>
              <li> "Show me pieces for slow mornings"</li>
              <li> "I need a gift for someone special"</li>
            </ul>
          </div>
          <div className="bg-white/80 backdrop-blur-sm rounded-3xl p-8 border border-stone-200/50">
            <h3 className="font-medium text-stone-800 mb-4 tracking-wide">AI Features</h3>
            <ul className="text-sm text-stone-600 space-y-2 font-light">
              <li> Visual similarity search</li>
              <li> Mood-based recommendations</li>
              <li> Thoughtful curation</li>
            </ul>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="flex-1 flex flex-col bg-gradient-to-br from-stone-50 via-amber-50/30 to-rose-50/30">
      {/* Active Agents Header */}
      <div className="bg-white/80 backdrop-blur-sm border-b border-stone-200/50 px-8 py-4">
        <div className="flex items-center space-x-6">
          <span className="text-sm font-medium text-stone-700 tracking-wide">Active specialists:</span>
          <div className="flex items-center space-x-3">
            {selectedBrands.slice(0, 4).map((brand) => (
              <div key={brand.id} className="flex items-center space-x-2 bg-white/60 rounded-2xl px-4 py-2 border border-stone-200/50">
                <img src={brand.icon} alt={brand.name} className="w-4 h-4 rounded" />
                <span className="text-sm text-stone-800 font-light tracking-wide">{brand.name}</span>
              </div>
            ))}
            {selectedBrands.length > 4 && (
              <span className="text-sm text-stone-500 font-light">+{selectedBrands.length - 4} more</span>
            )}
          </div>
        </div>
      </div>

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-8 space-y-8">
        {activeConversation?.messages.map((message) => (
          <div key={message.id} className={`flex ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}>
            <div className={`max-w-4xl ${message.sender === 'user' ? 'order-2' : 'order-1'}`}>
              {message.sender === 'ai' && message.brandId && (
                <div className="flex items-center space-x-3 mb-3">
                  {(() => {
                    const brand = selectedBrands.find(b => b.id === message.brandId);
                    return brand ? (
                      <>
                        <img src={brand.icon} alt={brand.name} className="w-6 h-6 rounded-xl" />
                        <span className="text-sm font-medium text-stone-700 tracking-wide">{brand.name}</span>
                        {message.confidence && (
                          <div className="flex items-center space-x-1">
                            <Heart className="w-3 h-3 text-rose-400" />
                            <span className="text-xs text-stone-500 font-light">{message.confidence}%</span>
                          </div>
                        )}
                      </>
                    ) : null;
                  })()}
                </div>
              )}
              
              <div className={`rounded-3xl p-6 ${
                message.sender === 'user' 
                  ? 'bg-gradient-to-r from-stone-700 to-stone-800 text-white' 
                  : 'bg-white/80 backdrop-blur-sm text-stone-800 border border-stone-200/50 shadow-sm'
              }`}>
                <p className="text-sm leading-relaxed font-light">{message.content}</p>
                
                {message.products && message.products.length > 0 && (
                  <div className="mt-6 grid grid-cols-1 md:grid-cols-2 gap-6">
                    {message.products.map((product) => (
                      <div key={product.id} className="bg-white/90 backdrop-blur-sm rounded-2xl p-6 border border-stone-200/50 shadow-sm hover:shadow-md transition-all">
                        <div className="aspect-square mb-4 overflow-hidden rounded-xl">
                          <img src={product.image} alt={product.name} className="w-full h-full object-cover hover:scale-105 transition-transform duration-300" />
                        </div>
                        <div className="space-y-3">
                          <h4 className="font-medium text-stone-800 tracking-wide">{product.name}</h4>
                          <p className="text-sm text-stone-600 font-light leading-relaxed">{product.description}</p>
                          <div className="flex items-center justify-between">
                            <div className="flex items-center space-x-3">
                              <span className="text-xl font-light text-stone-800">${product.price}</span>
                              {product.originalPrice && (
                                <span className="text-sm text-stone-400 line-through font-light">${product.originalPrice}</span>
                              )}
                            </div>
                            <div className="flex items-center space-x-2">
                              <button className="p-2 bg-stone-100 hover:bg-stone-200 rounded-xl transition-colors">
                                <Heart className="w-4 h-4 text-stone-600" />
                              </button>
                              <button 
                                onClick={() => handleOrder(product)}
                                className="bg-gradient-to-r from-stone-700 to-stone-800 text-white px-6 py-2 rounded-xl hover:from-stone-800 hover:to-stone-900 transition-all flex items-center space-x-2 font-light"
                              >
                                <ShoppingBag className="w-4 h-4" />
                                <span>Order</span>
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>
        ))}

        {streamingResponses.size > 0 && (
          <div className="space-y-6">
            {Array.from(streamingResponses.entries()).map(([brandId, response]) => {
              const brand = selectedBrands.find(b => b.id === brandId);
              if (!brand) return null;

              return (
                <div key={brandId} className="flex justify-start">
                  <div className="max-w-4xl">
                    <div className="flex items-center space-x-3 mb-3">
                      <img src={brand.icon} alt={brand.name} className="w-6 h-6 rounded-xl" />
                      <span className="text-sm font-medium text-stone-700 tracking-wide">{brand.name}</span>
                      <div className="flex items-center space-x-2 text-amber-600">
                        <Loader2 className="w-3 h-3 animate-spin" />
                        <span className="text-xs font-light">Curating...</span>
                      </div>
                    </div>
                    <div className="bg-white/80 backdrop-blur-sm border border-stone-200/50 rounded-3xl p-6 shadow-sm">
                      {response.content && (
                        <p className="text-sm text-stone-800 font-light">{response.content}</p>
                      )}
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        )}

        {activeConversations.length === 0 && !isTyping && (
          <div className="text-center py-16">
            <div className="w-20 h-20 bg-gradient-to-br from-amber-100 to-rose-100 rounded-3xl flex items-center justify-center mx-auto mb-6">
              <Sparkles className="w-10 h-10 text-stone-600" />
            </div>
            <h3 className="text-xl font-light text-stone-700 mb-4 tracking-wide">Start a conversation</h3>
            <p className="text-stone-500 mb-8 font-light">Describe your style, mood, or what you're looking for</p>
            <div className="flex flex-wrap justify-center gap-3">
              {[
                "Show me minimal winter pieces",
                "I need something for cozy evenings",
                "Find me a thoughtful gift",
                "Pieces for slow mornings"
              ].map((suggestion, index) => (
                <button
                  key={index}
                  onClick={() => handleSendMessage(suggestion)}
                  className="bg-white/80 hover:bg-white text-stone-700 px-6 py-3 rounded-2xl text-sm transition-all border border-stone-200/50 font-light tracking-wide"
                >
                  {suggestion}
                </button>
              ))}
            </div>
          </div>
        )}

        <div ref={messagesEndRef} />
      </div>

      {/* Message Input */}
      <div className="border-t border-stone-200/50 p-8 bg-white/80 backdrop-blur-sm">
        <div className="flex items-center space-x-4">
          <div className="flex-1 relative">
            <input
              type="text"
              value={currentMessage}
              onChange={(e) => setCurrentMessage(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
              placeholder="Describe your style, mood, or what you're seeking..."
              disabled={isTyping}
              className="w-full bg-white/60 border border-stone-200/50 rounded-2xl px-6 py-4 text-stone-800 placeholder-stone-500 focus:outline-none focus:ring-2 focus:ring-amber-200 focus:border-transparent font-light"
            />
            <button
              onClick={() => fileInputRef.current?.click()}
              className="absolute right-16 top-1/2 transform -translate-y-1/2 p-2 hover:bg-stone-100 rounded-xl transition-colors"
            >
              <Camera className="w-4 h-4 text-stone-500" />
            </button>
            <input
              ref={fileInputRef}
              type="file"
              accept="image/*"
              onChange={handleImageUpload}
              className="hidden"
            />
          </div>
          <button
            onClick={() => handleSendMessage()}
            disabled={!currentMessage.trim() || isTyping || selectedBrands.length === 0}
            className="bg-gradient-to-r from-stone-700 to-stone-800 text-white p-4 rounded-2xl hover:from-stone-800 hover:to-stone-900 disabled:from-stone-300 disabled:to-stone-400 disabled:cursor-not-allowed transition-all flex items-center justify-center"
          >
            {isTyping ? (
              <Loader2 className="w-5 h-5 animate-spin" />
            ) : (
              <Send className="w-5 h-5" />
            )}
          </button>
        </div>
      </div>

      {/* Order Form Modal */}
      {showOrderForm && selectedProduct && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-3xl p-8 max-w-md w-full border border-stone-200">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-light text-stone-800 tracking-wide">Complete Your Order</h2>
              <button
                onClick={() => setShowOrderForm(false)}
                className="text-stone-400 hover:text-stone-600"
              >
                <X className="w-5 h-5" />
              </button>
            </div>

            <div className="mb-6">
              <div className="flex items-center space-x-4 mb-6">
                <img
                  src={selectedProduct.image}
                  alt={selectedProduct.name}
                  className="w-16 h-16 rounded-2xl object-cover"
                />
                <div>
                  <h3 className="text-stone-800 font-medium tracking-wide">{selectedProduct.name}</h3>
                  <p className="text-stone-600 font-light">${selectedProduct.price}</p>
                </div>
              </div>

              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-stone-700 mb-2 tracking-wide">
                    <User className="inline w-4 h-4 mr-2" />
                    Full Name
                  </label>
                  <input
                    type="text"
                    value={orderForm.name}
                    onChange={(e) => setOrderForm({ ...orderForm, name: e.target.value })}
                    className="w-full bg-stone-50 border border-stone-200 rounded-2xl px-4 py-3 text-stone-800 focus:outline-none focus:ring-2 focus:ring-amber-200 font-light"
                    placeholder="Your name"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-stone-700 mb-2 tracking-wide">
                    <MapPin className="inline w-4 h-4 mr-2" />
                    Delivery Address
                  </label>
                  <input
                    type="text"
                    value={orderForm.address}
                    onChange={(e) => setOrderForm({ ...orderForm, address: e.target.value })}
                    className="w-full bg-stone-50 border border-stone-200 rounded-2xl px-4 py-3 text-stone-800 focus:outline-none focus:ring-2 focus:ring-amber-200 font-light"
                    placeholder="Your address"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-stone-700 mb-2 tracking-wide">
                    <Phone className="inline w-4 h-4 mr-2" />
                    Phone Number
                  </label>
                  <input
                    type="tel"
                    value={orderForm.phone}
                    onChange={(e) => setOrderForm({ ...orderForm, phone: e.target.value })}
                    className="w-full bg-stone-50 border border-stone-200 rounded-2xl px-4 py-3 text-stone-800 focus:outline-none focus:ring-2 focus:ring-amber-200 font-light"
                    placeholder="Your phone"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-stone-700 mb-2 tracking-wide">
                    <Package className="inline w-4 h-4 mr-2" />
                    Quantity
                  </label>
                  <input
                    type="number"
                    min="1"
                    value={orderForm.quantity}
                    onChange={(e) => setOrderForm({ ...orderForm, quantity: parseInt(e.target.value) || 1 })}
                    className="w-full bg-stone-50 border border-stone-200 rounded-2xl px-4 py-3 text-stone-800 focus:outline-none focus:ring-2 focus:ring-amber-200 font-light"
                  />
                </div>
              </div>

              <div className="mt-8 pt-6 border-t border-stone-200">
                <div className="flex justify-between items-center mb-6">
                  <span className="text-stone-600 font-light">Total:</span>
                  <span className="text-2xl font-light text-stone-800">
                    ${(selectedProduct.price * orderForm.quantity).toFixed(2)}
                  </span>
                </div>

                <button
                  onClick={handleSubmitOrder}
                  disabled={!orderForm.name || !orderForm.address || !orderForm.phone}
                  className="w-full bg-gradient-to-r from-stone-700 to-stone-800 text-white py-4 rounded-2xl font-light hover:from-stone-800 hover:to-stone-900 disabled:from-stone-300 disabled:to-stone-400 disabled:cursor-not-allowed transition-all tracking-wide"
                >
                  <CreditCard className="inline w-4 h-4 mr-2" />
                  Confirm Order
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Order Success */}
      {orderSuccess && (
        <div className="fixed bottom-8 right-8 bg-green-600 text-white px-8 py-4 rounded-2xl shadow-lg flex items-center space-x-3 z-50">
          <Check className="w-5 h-5" />
          <span className="font-light">Order confirmed! We'll be in touch soon.</span>
        </div>
      )}
    </div>
  );
};

export default JonkersChat;
