import React, { useState } from 'react';
import { useSubscribeDev } from '@subscribe.dev/react';
import JonkersAgentPanel from './AgentDiscoveryPanel';
import JonkersChat from './FederatedSearchInterface';
import UserButton from './UserButton';
import type { BrandAgent, CommerceConversation } from '../types';
import { ASSETS } from '../assetManifest';
import { Search, Sparkles, Heart, Camera, Mic, ShoppingBag, Palette, Home, MessageCircle, List, TrendingUp } from 'lucide-react';

const JonkersPlatform: React.FC = () => {
  const { isSignedIn, signIn, user, usage } = useSubscribeDev();
  const [selectedAgents, setSelectedAgents] = useState<BrandAgent[]>([]);
  const [activeConversations, setActiveConversations] = useState<CommerceConversation[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const [activeTab, setActiveTab] = useState<'chat' | 'collections' | 'insights'>('chat');

  if (!isSignedIn) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-stone-50 via-amber-50 to-rose-50 flex items-center justify-center p-4 relative overflow-hidden">
        <div className="absolute inset-0">
          <div className="absolute top-20 left-20 w-96 h-96 bg-amber-200/20 rounded-full blur-3xl animate-pulse"></div>
          <div className="absolute bottom-20 right-20 w-80 h-80 bg-rose-200/20 rounded-full blur-3xl animate-pulse delay-1000"></div>
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-64 h-64 bg-stone-200/20 rounded-full blur-3xl animate-pulse delay-500"></div>
        </div>

        <div className="relative bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl p-12 max-w-lg w-full mx-4 border border-stone-200/50">
          <div className="text-center mb-10">
            <div className="w-24 h-24 mx-auto mb-6 bg-gradient-to-br from-amber-100 to-rose-100 rounded-3xl flex items-center justify-center">
              <img src={ASSETS.brands.jonkers.path} alt="Jonkers" className="w-12 h-12" />
            </div>
            <h1 className="text-4xl font-light text-stone-800 mb-3 tracking-wide">Jonkers</h1>
            <p className="text-stone-600 text-lg font-light leading-relaxed">Your personal AI stylist for minimal, thoughtful design</p>
          </div>
          
          <div className="space-y-4">
            <button
              onClick={signIn}
              className="w-full bg-gradient-to-r from-stone-700 to-stone-800 text-white py-4 px-6 rounded-2xl font-medium hover:from-stone-800 hover:to-stone-900 transition-all transform hover:scale-105 shadow-lg"
            >
              Begin Your Journey
            </button>
            <p className="text-center text-stone-500 text-sm">Discover curated pieces that speak to your soul</p>
          </div>
        </div>
      </div>
    );
  }

  const handleAgentSelect = (agents: BrandAgent[]) => {
    setSelectedAgents(agents);
  };

  const tabs = [
    { id: 'chat' as const, label: 'Stylist', icon: MessageCircle },
    { id: 'collections' as const, label: 'Collections', icon: List },
    { id: 'insights' as const, label: 'Insights', icon: TrendingUp }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-stone-50 via-amber-50/30 to-rose-50/30 flex">
      {/* Collapsible Agent Sidebar */}
      <div className={`transition-all duration-300 ${sidebarCollapsed ? 'w-16' : 'w-80'} flex-shrink-0`}>
        <JonkersAgentPanel 
          selectedBrands={selectedAgents} 
          onBrandSelect={handleAgentSelect}
          collapsed={sidebarCollapsed}
          onToggleCollapse={() => setSidebarCollapsed(!sidebarCollapsed)}
        />
      </div>

      {/* Main Content Area */}
      <div className="flex-1 flex flex-col min-w-0">
        {/* Elegant Header */}
        <header className="bg-white/80 backdrop-blur-xl border-b border-stone-200/50 px-8 py-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-6">
              <div className="flex items-center space-x-4">
                <div className="w-10 h-10 bg-gradient-to-br from-amber-100 to-rose-100 rounded-2xl flex items-center justify-center">
                  <img src={ASSETS.brands.jonkers.path} alt="Jonkers" className="w-6 h-6" />
                </div>
                <div>
                  <h1 className="text-xl font-light text-stone-800 tracking-wide">Jonkers</h1>
                  <p className="text-xs text-stone-500 font-light">AI Styling Intelligence</p>
                </div>
              </div>
            </div>

            {/* Elegant Search Bar */}
            <div className="flex-1 max-w-2xl mx-8">
              <div className="relative">
                <Search className="absolute left-4 top-1/2 transform -translate-y-1/2 w-5 h-5 text-stone-400" />
                <input
                  type="text"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  placeholder="Describe your style, mood, or what you're looking for..."
                  className="w-full bg-white/60 border border-stone-200/50 rounded-2xl pl-12 pr-16 py-4 text-stone-800 placeholder-stone-500 focus:outline-none focus:ring-2 focus:ring-amber-200 focus:border-transparent backdrop-blur-sm"
                />
                <div className="absolute right-3 top-1/2 transform -translate-y-1/2 flex items-center space-x-2">
                  <button className="p-2 hover:bg-stone-100 rounded-xl transition-colors">
                    <Camera className="w-4 h-4 text-stone-500" />
                  </button>
                  <button className="p-2 hover:bg-stone-100 rounded-xl transition-colors">
                    <Mic className="w-4 h-4 text-stone-500" />
                  </button>
                </div>
              </div>
            </div>

            <div className="flex items-center space-x-6">
              <div className="flex items-center space-x-3 bg-white/60 rounded-2xl px-4 py-2 border border-stone-200/50">
                <Sparkles className="w-4 h-4 text-amber-500" />
                <span className="text-sm text-stone-700 font-medium">
                  {usage?.remainingCredits || 0} credits
                </span>
              </div>
              <UserButton user={user} />
            </div>
          </div>
        </header>

        {/* Soft Tab Navigation */}
        <div className="bg-white/60 backdrop-blur-sm border-b border-stone-200/50 px-8">
          <nav className="flex space-x-12">
            {tabs.map((tab) => {
              const Icon = tab.icon;
              return (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id)}
                  className={`flex items-center space-x-3 py-6 px-2 border-b-2 font-medium text-sm transition-all ${
                    activeTab === tab.id
                      ? 'border-stone-600 text-stone-800'
                      : 'border-transparent text-stone-500 hover:text-stone-700 hover:border-stone-300'
                  }`}
                >
                  <Icon className="w-4 h-4" />
                  <span className="font-light tracking-wide">{tab.label}</span>
                </button>
              );
            })}
          </nav>
        </div>

        {/* Tab Content */}
        <main className="flex-1 overflow-hidden">
          {activeTab === 'chat' && (
            <JonkersChat 
              selectedBrands={selectedAgents}
              searchQuery={searchQuery}
              activeConversations={activeConversations}
              setActiveConversations={setActiveConversations}
            />
          )}
          
          {activeTab === 'collections' && (
            <div className="h-full bg-gradient-to-br from-stone-50 via-amber-50/30 to-rose-50/30 p-8">
              <div className="max-w-7xl mx-auto">
                <div className="text-center mb-12">
                  <h2 className="text-3xl font-light text-stone-800 mb-4 tracking-wide">Curated Collections</h2>
                  <p className="text-stone-600 font-light text-lg">Thoughtfully assembled pieces for every moment</p>
                </div>
                
                {selectedAgents.length === 0 ? (
                  <div className="text-center py-16">
                    <div className="w-20 h-20 bg-gradient-to-br from-amber-100 to-rose-100 rounded-3xl flex items-center justify-center mx-auto mb-6">
                      <Palette className="w-10 h-10 text-stone-600" />
                    </div>
                    <h3 className="text-xl font-light text-stone-800 mb-3">Select your style agents</h3>
                    <p className="text-stone-600 font-light">Choose specialists from the sidebar to see curated collections</p>
                  </div>
                ) : (
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {selectedAgents.map((agent) => (
                      <div key={agent.id} className="bg-white/80 backdrop-blur-sm rounded-3xl shadow-lg border border-stone-200/50 p-8 hover:shadow-xl transition-all hover:scale-105">
                        <div className="flex items-center space-x-4 mb-6">
                          <img src={agent.icon} alt={agent.name} className="w-12 h-12 rounded-2xl" />
                          <div>
                            <h3 className="font-medium text-stone-800 tracking-wide">{agent.name}</h3>
                            <p className="text-sm text-stone-500 font-light capitalize">{agent.category}</p>
                          </div>
                        </div>
                        <p className="text-sm text-stone-600 mb-6 font-light leading-relaxed">{agent.description}</p>
                        <button className="w-full bg-gradient-to-r from-stone-600 to-stone-700 text-white py-3 rounded-2xl hover:from-stone-700 hover:to-stone-800 transition-all font-light tracking-wide">
                          Explore Collection
                        </button>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          )}
          
          {activeTab === 'insights' && (
            <div className="h-full bg-gradient-to-br from-stone-50 via-amber-50/30 to-rose-50/30 p-8">
              <div className="max-w-7xl mx-auto">
                <h2 className="text-3xl font-light text-stone-800 mb-12 tracking-wide">Style Insights</h2>
                
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-12">
                  <div className="bg-white/80 backdrop-blur-sm rounded-3xl p-8 shadow-sm border border-stone-200/50">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-light text-stone-600 mb-2">Style Conversations</p>
                        <p className="text-3xl font-light text-stone-800">
                          {selectedAgents.reduce((sum, agent) => sum + agent.totalInteractions, 0).toLocaleString()}
                        </p>
                      </div>
                      <div className="w-12 h-12 bg-gradient-to-br from-amber-100 to-rose-100 rounded-2xl flex items-center justify-center">
                        <MessageCircle className="w-6 h-6 text-stone-600" />
                      </div>
                    </div>
                  </div>
                  
                  <div className="bg-white/80 backdrop-blur-sm rounded-3xl p-8 shadow-sm border border-stone-200/50">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-light text-stone-600 mb-2">Satisfaction Score</p>
                        <p className="text-3xl font-light text-stone-800">
                          {selectedAgents.length > 0 ? 
                            (selectedAgents.reduce((sum, agent) => sum + agent.satisfactionScore, 0) / selectedAgents.length).toFixed(1) : 0}%
                        </p>
                      </div>
                      <div className="w-12 h-12 bg-gradient-to-br from-green-100 to-emerald-100 rounded-2xl flex items-center justify-center">
                        <Heart className="w-6 h-6 text-stone-600" />
                      </div>
                    </div>
                  </div>
                  
                  <div className="bg-white/80 backdrop-blur-sm rounded-3xl p-8 shadow-sm border border-stone-200/50">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-light text-stone-600 mb-2">Avg. Investment</p>
                        <p className="text-3xl font-light text-stone-800">
                          ${selectedAgents.length > 0 ? 
                            (selectedAgents.reduce((sum, agent) => sum + agent.averageOrderValue, 0) / selectedAgents.length).toFixed(0) : 0}
                        </p>
                      </div>
                      <div className="w-12 h-12 bg-gradient-to-br from-purple-100 to-pink-100 rounded-2xl flex items-center justify-center">
                        <ShoppingBag className="w-6 h-6 text-stone-600" />
                      </div>
                    </div>
                  </div>
                  
                  <div className="bg-white/80 backdrop-blur-sm rounded-3xl p-8 shadow-sm border border-stone-200/50">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-light text-stone-600 mb-2">Style Match Rate</p>
                        <p className="text-3xl font-light text-stone-800">
                          {selectedAgents.length > 0 ? 
                            (selectedAgents.reduce((sum, agent) => sum + agent.conversionRate, 0) / selectedAgents.length).toFixed(1) : 0}%
                        </p>
                      </div>
                      <div className="w-12 h-12 bg-gradient-to-br from-blue-100 to-indigo-100 rounded-2xl flex items-center justify-center">
                        <Sparkles className="w-6 h-6 text-stone-600" />
                      </div>
                    </div>
                  </div>
                </div>

                {selectedAgents.length > 0 && (
                  <div className="bg-white/80 backdrop-blur-sm rounded-3xl shadow-sm border border-stone-200/50 p-8">
                    <h3 className="text-xl font-light text-stone-800 mb-8 tracking-wide">Agent Performance</h3>
                    <div className="space-y-6">
                      {selectedAgents.map((agent) => (
                        <div key={agent.id} className="flex items-center justify-between p-6 bg-stone-50/50 rounded-2xl">
                          <div className="flex items-center space-x-4">
                            <img src={agent.icon} alt={agent.name} className="w-10 h-10 rounded-2xl" />
                            <div>
                              <p className="font-medium text-stone-800 tracking-wide">{agent.name}</p>
                              <p className="text-sm text-stone-500 font-light">{agent.totalInteractions.toLocaleString()} conversations</p>
                            </div>
                          </div>
                          <div className="text-right">
                            <p className="font-medium text-stone-800">{agent.satisfactionScore}% satisfaction</p>
                            <p className="text-sm text-stone-500 font-light">{agent.conversionRate}% match rate</p>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            </div>
          )}
        </main>
      </div>
    </div>
  );
};

export default JonkersPlatform;
